import React from "react";
import { Modal, Image } from "react-bootstrap";
import { WASABI_PATH } from "../../actions/api";
import { OPEN_INTERCOM, updateInfoToIC } from "../../utils";

const InvalidEmail = ({ show, onHide }) => {

    const openIntercom = () => {
        updateInfoToIC({ app_id: 'veiaqij8' });
        onHide();
        setTimeout(OPEN_INTERCOM, 500);
    }

    return <Modal size="sm" show={show} className="px-3" onHide={onHide} backdrop="static" aria-labelledby="contained-modal-title-vcenter" keyboard={false} centered>
        <Modal.Body className="p-0">
            <div className="popup-wrap">
                <div className="popup-inn">
                    <div className="popup-head">
                        <Image className="py-2" src={`${WASABI_PATH}app/assets/popup/error-email-dark.png`} alt="error-email" />
                    </div>
                    <p>If you think your email id valid, please <span className="text-primary" onClick={openIntercom}>Talk to us!!</span>
                    <span className="popup-try d-inline-block rounded-pill" style={{backgroundColor: 'var(--bs-gray-100)', marginTop: '10px', padding: '5px 15px'}} onClick={onHide}>Try Another Email</span></p>
                </div>
            </div>
        </Modal.Body>
    </Modal>
}

export default InvalidEmail;